/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #1a1b15; }

p {
  color: #FFFFFF;
  font-size: 2rem; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #0da6f3;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

@media (min-width: 768px) {
  .app-container {
    height: 100vh;
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr); } }

.app-container .img {
  height: 30rem;
  background-image: url(../img/1.webp);
  background-size: cover;
  background-position: center center; }
  @media (min-width: 768px) {
    .app-container .img {
      height: auto; } }

.app-container .app {
  padding: 2.5rem; }

.tabs {
  justify-content: space-between;
  gap: 1.66667rem; }
  .tabs button {
    font-weight: 700;
    border: none;
    width: 100%;
    padding: 1rem 0;
    text-transform: uppercase; }
    .tabs button:hover {
      cursor: pointer; }
    .tabs button.active {
      background-color: #0da6f3;
      color: #FFFFFF; }

.section {
  display: none;
  margin-top: 2.5rem; }

.show-section {
  display: block !important; }

.d-none {
  display: none; }

@media (min-width: 768px) {
  .services-list {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr); } }

.services-list .service {
  background-color: #FFFFFF;
  padding: 1.66667rem;
  border-radius: 1rem;
  margin: 1rem 0;
  cursor: pointer; }
  .services-list .service:hover {
    transform: scale(1.02);
    transition: 200ms ease-in-out; }
  @media (min-width: 768px) {
    .services-list .service {
      margin: 0; } }
  .services-list .service p {
    margin: 1rem; }
  .services-list .service.select {
    background-color: #0da6f3; }
    .services-list .service.select p {
      color: #FFFFFF; }

.services-list .service-name {
  color: #1a1b15;
  font-weight: 400; }

.services-list .service-price {
  color: #0da6f3;
  font-size: 2.5rem;
  font-weight: 700; }

.arrow-btn {
  margin-top: 2.5rem;
  justify-content: space-between; }
  .arrow-btn button {
    border: none;
    background-color: #0da6f3;
    color: #FFFFFF;
    text-transform: uppercase;
    font-weight: 700;
    padding: 1rem 4rem; }
    .arrow-btn button:hover {
      cursor: pointer; }
    .arrow-btn button.hide {
      visibility: hidden; }

.form {
  flex-direction: column; }
  .form div {
    margin: 1rem 0;
    gap: 1rem;
    align-items: center; }
  .form input {
    height: 5rem;
    padding: 0 1rem;
    overflow: hidden; }
  .form label {
    color: #FFFFFF;
    width: 6rem; }
  .form input {
    width: 100%;
    border: none;
    border-radius: 1rem; }

.red-alert {
  background-color: #eb2f06;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  padding: 1rem; }

.green-alert {
  background-color: #009432;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  padding: 1rem; }

.overview-content h3 {
  margin: 2.5rem 0; }

.overview-content strong {
  color: #0da6f3; }

.overview-content .total {
  text-align: right; }

.overview-text {
  margin: 0;
  line-height: 5rem; }

.overview-list {
  list-style: none;
  padding: 0;
  margin: 0;
  line-height: 5rem; }
  .overview-list strong {
    color: #0da6f3; }

.overview-item {
  color: #FFFFFF;
  font-size: 2rem;
  border-bottom: 1px solid #43453c; }
